.modal {
    width: 500px;
    height: 300px;
    border: 10px solid blue;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 30%;
    left: 35%;
    /* transition: all 1s ease; */
}

button:hover {
    box-shadow: 0px 0px 20px 4px #144d98;
}

.position {
    width: 500px;
    height: 300px;
    border: 10px solid blue;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 30%;
    left: 35%;
}

.btn {
    width: auto;
    position: absolute;
    top: 0;
    right: 0;
}

p {
    color: #000;
    font-size: 34px;
    text-align: center;
    font-weight: bold;
}

.flex {
    display: flex;
}


button {
    width: 100px;
    margin: 15px;
    border: 3px solid blue;
}